import React from 'react'
import { mergeAll, map, filter, keys } from 'ramda'
import { QueryVariables } from '@/constants/types'
import useSWR from '@/hooks/useSWR'
import { request } from '@/utils/request'
const { compose } = require('ramda')

export default function useQuery<T = any>(
  url: string,
  {
    lazy = true,
    method = 'POST',
    variables: { onCompleted, onError, ...variables } = {},
  }: {
    method?: 'POST' | 'GET'
    variables?: QueryVariables
    lazy?: boolean
  }
) {
  const [query, setQuery] = React.useState(variables)

  const fetcher = (_url: string, ...args: any) => {
    return request(_url, {
      ...args,
      method,
      // remove nil or undefined key/value
      ...(method === 'POST'
        ? {
            body: compose(
              mergeAll,
              map((key: string) => ({ [key]: query[key] })),
              filter(
                (key: string) =>
                  query[key] !== null &&
                  query[key] !== undefined &&
                  query[key] !== ''
              ),
              keys
            )(query),
          }
        : {}),
      throwable: true,
    })
  }

  const { data, refetch, ...rest } = useSWR<T>(
    url,
    {
      lazy,
      onCompleted,
      onError,
      variables,
    },
    fetcher
  )

  const _refetch = (variables: QueryVariables = {}) => {
    setQuery(variables)
    refetch(variables)
  }

  return {
    ...rest,
    refetch: _refetch,
    data,
  }
}
