import React from 'react'
import { Col, Row, Form, Tooltip } from 'antd'
import { BulbOutlined, DoubleLeftOutlined } from '@ant-design/icons'
import { split, pathOr, join } from 'ramda'
import Collapse from '@/components/collapse'
import Input from '@/components/input'
import SnippetListMenu from '@/components/snippet-list-menu'
import { ReportTemplateSelect } from '@/services/report-template'
import { Snippets } from '@/constants/types'
import useAuth from '@/hooks/useAuth'
import ReportAffix from './report-typing-affix'
const { compose } = require('ramda')

type Props = {
  category: string
  templateName: string
  snippetList: Snippets
  onTabChange: (activeKey: string) => void
  onLayoutChnage: () => void
  onReportChange: (value: string) => void
  onPhraseListModalVisible: () => void
}

export default function ReportTypingSideMenu(props: Props) {
  const {
    onLayoutChnage,
    onReportChange,
    onTabChange,
    onPhraseListModalVisible,
    category,
    templateName,
    snippetList,
  } = props

  const { user } = useAuth()

  const [affixed, setIsAffixed] = React.useState<boolean | undefined>(false)

  const handleAffixChange = (affixed?: boolean) => {
    setIsAffixed(affixed)
  }

  const handleAnchorClick = (
    e: React.MouseEvent<HTMLElement>,
    link: {
      title: React.ReactNode
      href: string
    }
  ) => {
    e.preventDefault()

    const [tab, href] = compose(
      ([tab, ...rest]: string[]) => [tab, join('-')(rest)],
      split('-'),
      pathOr('', ['href'])
    )(link)

    if (category !== 'overview') {
      onTabChange(tab)
    }

    if (!href) {
      return
    }

    window.scroll({
      behavior: 'smooth',
      left: 0,
      top: document.getElementById(href)?.offsetTop,
    })
  }

  return (
    <>
      <Col span={5}>
        <Collapse
          defaultActiveKey={[1, 2]}
          expandIcon={() => null}
          className="shadow-2xl">
          <Collapse.Panel
            header={
              <div className="text-center relative">
                報告內容
                <Tooltip title="收合報告內容">
                  <DoubleLeftOutlined
                    className="absolute cursor-pointer"
                    style={{ top: 5, right: 0 }}
                    onClick={onLayoutChnage}
                  />
                </Tooltip>
              </div>
            }
            key={1}>
            <Row className="mb-2 text-sm text-darkGrey">
              <Col span={24} className="mb-3">
                <div className="inline-block" css={{ width: 65 }}>
                  判讀醫師
                </div>
                <Form.Item noStyle name="doctor" initialValue={user.name}>
                  <Input className="w-2/3" bordered={false} />
                </Form.Item>
              </Col>
              <Col span={24} className="my-3">
                <div className="inline-block" css={{ width: 65 }}>
                  申請序號
                </div>
                <Form.Item noStyle name="orreqno">
                  <Input className="w-2/3" bordered={false} />
                </Form.Item>
              </Col>
              <Col span={24} className="my-3">
                <div className="inline-block" css={{ width: 65 }}>
                  報告格式
                </div>
                <Form.Item
                  noStyle
                  name="templateName"
                  initialValue={templateName}>
                  <ReportTemplateSelect
                    className="w-2/3"
                    onChange={onReportChange}
                  />
                </Form.Item>
              </Col>
              {category === 'overview' && (
                <Col span={24} className="mt-3">
                  <ReportAffix
                    affixed={affixed}
                    onClick={handleAnchorClick}
                    onChange={handleAffixChange}
                    snippetList={snippetList}
                  />
                </Col>
              )}
            </Row>
          </Collapse.Panel>
          <Collapse.Panel
            header={<div className="text-center">模版工具</div>}
            key={2}>
            <Row>
              <Col span={24}>
                <SnippetListMenu />
                <div
                  className="flex items-center mt-2 cursor-pointer"
                  onClick={onPhraseListModalVisible}>
                  <BulbOutlined className="mr-2" />
                  Phrase
                </div>
              </Col>
            </Row>
          </Collapse.Panel>
        </Collapse>
      </Col>
    </>
  )
}
