import React, { useEffect } from 'react'
import { RouteComponentProps } from 'react-router-dom'
import { Row, Col, BackTop, Affix, Form, message, Tooltip, Spin } from 'antd'
import { LeftOutlined, RightOutlined } from '@ant-design/icons'
import cx from 'classnames'
import moment from 'moment'
import {
  map,
  pathOr,
  keys,
  gt,
  __,
  length,
  ifElse,
  equals,
  always,
  filter,
  values,
  not,
  replace,
  join,
  toPairs,
  head,
  update,
  is,
  T,
  identity,
} from 'ramda'
import useTitle from '@/hooks/useTitle'
import useVisible from '@/hooks/useVisible'
import { useBreadcrumb, useHeaderContent } from '@/hooks/useApp'
import useAuth from '@/hooks/useAuth'
import { isNil, uuid, mapIndexed } from '@/utils/webHelper'
import { SnippetProps } from '@/constants/types'
import { Page } from '@/components/layout'
import ReactDndProvider from '@/components/snippet-scrollable-container'
import SnippetsContainer from '@/components/snippets-container'
import { renderSnippets } from '@/components/snippet-utils'
import Tabs from '@/components/draggable-tabs'
import {
  useToggleReportPreview,
  useAllReportTemplates,
} from '@/services/report-template'
import { useCreateOrUpdateReport } from '@/services/report'
import history from '@/utils/history'
import { REPORT_EDIT } from '@/constants/routes'
import Pie from './pie'
import Header from './report-typing-header'
import ReportMedicalOrder from './report-typing-medical-order'
import ReportPreview from './report-typing-preview'
import ReportSideMenu from './report-typing-sidemenu'
import PhraseListModal from './phrase-list-modal'
import useReport from './useReport'
import useLayout from './useLayout'
import { Divisions } from '@/constants/enums'
const { compose, cond } = require('ramda')

interface Props extends RouteComponentProps<{ id: string }> {
  id: any
  doctor: string
  orreqno: string
}

const col = ifElse(equals(false), always(5), always(0))

export default function ReportTyping(props: Props) {
  const {
    doctor,
    match: { params },
  } = props

  const { user } = useAuth()

  useTitle('輸入報告')

  useBreadcrumb([{ name: '輸入報告' }, { name: '病理報告輸入' }])

  useToggleReportPreview(true)

  useEffect(() => {
    const handleKeyPress = (evt: KeyboardEvent) => {
      if (exportable) {
        return
      }

      // ctrl + s
      if (evt.ctrlKey && evt.key === 'S') {
        handleOk()
      }
      // f9
      if (!evt.ctrlKey && evt.key === 'F9') {
        handlePhraseListModalVisible()
      }
    }

    window.addEventListener('keydown', handleKeyPress)

    return () => {
      window.removeEventListener('keydown', handleKeyPress)
    }
  }, []) // eslint-disable-line

  useEffect(() => {
    if (user.division !== Divisions.PATH && user.division !== Divisions.RAD) {
      handleExportable()
    }
  }, [user.division]) // eslint-disable-line

  const [form] = Form.useForm()

  const [exportable, handleExportable] = useVisible()

  const [phraseListModalVisible, handlePhraseListModalVisible] = useVisible()

  const {
    setVisibleLayouts,
    leftContentVisible,
    rightContentVisible,
    toggleLeftMenu,
    toggleRightMenu,
  } = useLayout()

  const {
    loading,
    total,
    category,
    reportTemplate,
    snippetList,
    handleTabEdit,
    handleTabChange,
    handleTabMove,
    handleTabNameChange,
    handleSnippetUpdate,
    setCurrentTemplateName,
    templateName,
    medicalorder,
    readOnly,
  } = useReport({
    ...props,
    form,
    setVisibleLayouts,
    onExportableChange: handleExportable,
  })

  useEffect(() => {
    console.log(reportTemplate)
  }, [JSON.stringify(reportTemplate.data)]) // eslint-disable-line

  const [creating, handleCreateOrUpdate] = useCreateOrUpdateReport({
    onCompleted: response => {
      history.push(replace(':id', response?.data.orreqno)(REPORT_EDIT))
      message.success('報告已儲存成功')
    },
  })

  const { templateByName } = useAllReportTemplates()

  const handleOk = () => {
    form
      .validateFields()
      .then(values => {
        handleCreateOrUpdate({
          ...values,
          ...(params.id ? { orreqno: params.id } : {}),
          hhisnum: medicalorder?.hhisnum || reportTemplate.hhisnum,
          templateId: pathOr(
            reportTemplate.templateId,
            [values.templateName, 'id'],
            templateByName
          ),
          setting: snippetList,
        })
      })
      .catch(error => {
        message.config({ maxCount: 1 })
        message.error(
          compose(
            join('\n'),
            map(
              (x: any) =>
                `${compose(
                  pathOr('', []),
                  head,
                  pathOr([], ['name'])
                )(x)}: ${compose(join('、'), pathOr([], ['errors']))(x)}`
            ),
            pathOr([], ['errorFields'])
          )(error)
        )
      })
  }

  useHeaderContent(
    <Header
      readOnly={readOnly}
      data={reportTemplate}
      isExport={exportable}
      onToggleExport={handleExportable}
      onOk={handleOk}
    />
  )

  const [percentage, setPercentage] = React.useState(0)

  const handlePhraseAdd = (values: SnippetProps[]) => {
    let newList = [
      ...snippetList[category],
      ...values.map(x => ({
        ...x,
        fromMenu: false,
        id: `${x.id}__${uuid()}`,
      })),
    ]

    handleSnippetUpdate(newList)
  }

  type FormValuesChangeHandler = (
    changedValues: any,
    data: { data: { [key: string]: any } }
  ) => void

  // prettier-ignore
  const handleValuesChange: FormValuesChangeHandler = (changedValues, { data }) => {
    setPercentage(
      Math.round(
        (compose(length, filter(compose(not, isNil)), values)(data) / total) *
          100
      )
    )

    if (!changedValues) {
      return
    }

    const [id, value] = compose(
      pathOr([], []),
      head,
      toPairs,
      pathOr({}, ['data'])
    )(changedValues)

    if (!id) {
      return
    }

    const list = snippetList[category]
    const snippet = list.filter(x => x.id === id)[0]
    const index = list.indexOf(snippet)

    handleSnippetUpdate(
      update(
        index,
        {
          ...snippet,
          setting: {
            ...snippet.setting,
            value: cond([
              [is(Array), join('、')],
              [
                (val: any) => moment.isMoment(val),
                (val: any) =>
                  snippet.type === 'TemplateDate'
                    ? val.format('YYYY/MM/DD')
                    : val.format('HH:mm:ss'),
              ],
              [T, identity],
            ])(value),
          },
        },
        list
      )
    )
  }

  React.useEffect(() => {
    handleValuesChange(null, form.getFieldsValue())
  }, [total]) // eslint-disable-line

  const renderCategory = (category: string) => {
    return compose(gt(__, 0), length, pathOr([], [category]))(snippetList) ? (
      <h2 id={category} className="my-2">
        {category}
      </h2>
    ) : null
  }

  const renderORWord = (idx: number) => {
    const orword = pathOr(reportTemplate?.orword, ['orword'], medicalorder)
    return (
      !!orword &&
      idx === 0 && (
        <div
          className="mb-4 px-1"
          dangerouslySetInnerHTML={{
            __html: `${orword}`,
          }}
        />
      )
    )
  }

  const colSpan = 14 + col(leftContentVisible) + col(rightContentVisible)

  return (
    <ReactDndProvider className="is--preview">
      {phraseListModalVisible && (
        <PhraseListModal
          onOk={handlePhraseAdd}
          onCancel={handlePhraseListModalVisible}
        />
      )}
      <BackTop />
      <Spin spinning={loading || creating}>
        <Page>
          <Form
            form={form}
            labelAlign={exportable ? 'left' : 'right'}
            className="relative"
            onValuesChange={handleValuesChange}>
            {!leftContentVisible && !readOnly && (
              <Tooltip title="展開報告內容">
                <div
                  className="absolute z-10 bg-secondary text-white rounded-tr-md rounded-br-md flex items-center justify-center cursor-pointer py-2 pl-1"
                  css={{ left: -40, top: 7 }}
                  onClick={toggleLeftMenu}>
                  <RightOutlined className="text-xl" />
                </div>
              </Tooltip>
            )}
            <Row gutter={16}>
              {leftContentVisible && (
                <ReportSideMenu
                  onLayoutChnage={toggleLeftMenu}
                  onReportChange={setCurrentTemplateName}
                  onTabChange={handleTabChange}
                  onPhraseListModalVisible={handlePhraseListModalVisible}
                  snippetList={snippetList}
                  category={category}
                  templateName={templateName}
                />
              )}
              <Col span={colSpan}>
                {!exportable && (
                  <>
                    <div className="pt-2 bg-white rounded-lg shadow-2xl">
                      <Affix offsetTop={0}>
                        <div
                          className={cx(
                            'px-4 mb-4 -mt-2',
                            'flex items-center justify-between',
                            'rounded-lg shadow-sm',
                            'text-xl text-darkGrey font-medium'
                          )}
                          css={{ background: '#fff' }}>
                          <div>
                            {user.division === Divisions.PATH
                              ? 'Pathological'
                              : 'Radiological'}{' '}
                            Image Report
                            <div className="text-base text-gray-600"></div>
                          </div>
                          <Pie
                            percent={percentage}
                            total={`${percentage || 0}%`}
                            style={{ width: 70, paddingTop: 6 }}
                            height={80}
                          />
                        </div>
                      </Affix>
                    </div>
                    <div className="rounded-lg shadow-2xl">
                      <Tabs
                        type="editable-card"
                        activeKey={category}
                        onChange={handleTabChange}
                        onEdit={handleTabEdit}
                        tabBarGutter={8}
                        moveTabNode={handleTabMove}
                        className="flex-1">
                        {compose(
                          mapIndexed((category: string, idx: number) => (
                            <Tabs.TabPane
                              closable
                              key={category}
                              tab={
                                <span
                                  contentEditable
                                  suppressContentEditableWarning
                                  spellCheck={false}
                                  className="outline-none"
                                  css={{ whiteSpace: 'nowrap' }}
                                  onBlur={handleTabNameChange(category)}>
                                  {category}
                                </span>
                              }>
                              <Col
                                span={24}
                                className="p-4 report-template-builder__main-content">
                                {renderORWord(idx)}
                                <SnippetsContainer
                                  // draggable={!isPreview}
                                  draggable
                                  snippetList={snippetList[category]}
                                  onSnippetUpdate={handleSnippetUpdate}
                                />
                              </Col>
                            </Tabs.TabPane>
                          )),
                          keys
                        )(snippetList)}
                        <Tabs.TabPane key="overview" tab="Overview">
                          {compose(
                            map((category: string) => (
                              <div key={category} className="px-3 bg-white">
                                {renderCategory(category)}
                                {compose(
                                  renderSnippets({ className: 'mb-0' }),
                                  pathOr([], [category])
                                )(snippetList)}
                              </div>
                            )),
                            keys
                          )(snippetList)}
                        </Tabs.TabPane>
                      </Tabs>
                    </div>
                  </>
                )}
                {exportable && (
                  <div className="rounded-log shadow-2x bg-white p-3 px-5">
                    <div id="editor" />
                    <ReportPreview
                      data={{
                        template: reportTemplate,
                        medical: medicalorder,
                        snippetList,
                      }}
                    />
                  </div>
                )}
              </Col>

              {rightContentVisible && (
                <Col span={5}>
                  <ReportMedicalOrder
                    data={
                      user.division === Divisions.PATH
                        ? medicalorder
                        : (reportTemplate as any)
                    }
                    doctor={doctor}
                    onToggle={toggleRightMenu}
                  />
                </Col>
              )}
            </Row>
            {!rightContentVisible && !readOnly && (
              <Tooltip title="展開醫囑內容">
                <div
                  className="absolute z-10 bg-secondary text-white rounded-tl-md rounded-bl-md flex items-center justify-center cursor-pointer py-2 pr-1"
                  css={{ right: -49, top: 7 }}
                  onClick={toggleRightMenu}>
                  <LeftOutlined className="text-xl" />
                </div>
              </Tooltip>
            )}
          </Form>
        </Page>
      </Spin>
      ?
    </ReactDndProvider>
  )
}
