import React from 'react'
import { RouteComponentProps } from 'react-router-dom'
import { FormInstance } from 'antd/lib/form'
import { message } from 'antd'
import { equals } from 'ramda'
import { queryString } from '@/utils/webHelper'
import history from '@/utils/history'
import { REPORT_INQUIRY } from '@/constants/routes'
import { useReportItem, useMedicalOrder } from '@/services/report'
import { useReportTemplate } from '@/services/report-template'

interface Props extends RouteComponentProps<{ id: string }> {
  id: any
  doctor: string
  orreqno: string
  form: FormInstance
  setVisibleLayouts: React.Dispatch<React.SetStateAction<string[]>>
  onExportableChange: () => void
}

export default function useReport({
  form,
  setVisibleLayouts,
  onExportableChange,
  ...props
}: Props) {
  // 沒有 QS 代表是編輯，會有 params.id 要用 useReportItem 取得報告資料後，
  // 再餵進去 useReportTemplate 作編輯
  const { params } = props.match

  // 有 QS 代表狀態是新增，此時沒有 params.id，這時的資料要用 reportTemplate
  const qs = queryString(props.location.search)

  // reset form field values while qs changed
  React.useEffect(() => {
    if (qs.doctor) {
      form.setFieldsValue({ doctor: qs.doctor })
    }
    if (qs.orreqno) {
      form.setFieldsValue({ orreqno: qs.orreqno })
    }
    if (!!qs.finish) {
      setVisibleLayouts([])
      onExportableChange()
    }
  }, [JSON.stringify(qs)]) // eslint-disable-line

  // 新增時使用 QS 的 orreqno，編輯用 pramas.id (/report-input/:id) 的
  const orreqno = params.id || qs.orreqno

  // reset templateName state while qs.templateName changed
  React.useEffect(() => {
    setCurrentTemplateName(qs.templateName)
  }, [qs.templateName])

  // 只有新增時有 QS 才會有
  const [templateName, setCurrentTemplateName] = React.useState(qs.templateName)

  const { data: medicalorder, loadingM } = useMedicalOrder({ id: orreqno })

  const { item, loading: loadingData } = useReportItem({
    id: orreqno,
    onError: response => {
      if (equals('ORREQNO is not exist.', response?.message)) {
        history.push(REPORT_INQUIRY)
        message.error('申請序號不存在，請重新輸入')
      }
    },
  })

  const { loading, reportTemplate, ...rest } = useReportTemplate({
    id: templateName,
    data: item,
  })

  // reset form field values while report data changed
  React.useEffect(() => {
    if (reportTemplate) {
      form.setFieldsValue(reportTemplate)
    }
    // avoid use report template status
    if (reportTemplate.status === 's') {
      form.setFieldsValue({ status: undefined })
    }
    if (reportTemplate.templateName) {
      form.setFieldsValue({ templateId: reportTemplate.templateName })
    }
  }, [JSON.stringify(reportTemplate)]) // eslint-disable-line

  const _loading = loading || loadingM || loadingData

  return {
    ...rest,
    templateName,
    reportTemplate,
    medicalorder,
    setCurrentTemplateName,
    readOnly: !!qs.finish,
    loading: _loading,
  }
}
