import React from 'react'

enum menuState {
  VISIBLE_RIGHT = '1',
  VISIBLE_LEFT = '2',
}

export default function useLayout() {
  const [visibleLayouts, setVisibleLayouts] = React.useState<string[]>([
    menuState.VISIBLE_LEFT,
    menuState.VISIBLE_RIGHT,
  ])

  const handleLayoutChange = (value: string) => () => {
    setVisibleLayouts((prev: string[]) =>
      prev.includes(value) ? prev.filter(x => x !== value) : [...prev, value]
    )
  }

  const toggleLeftMenu = handleLayoutChange(menuState.VISIBLE_LEFT)
  const toggleRightMenu = handleLayoutChange(menuState.VISIBLE_RIGHT)

  return {
    visibleLayouts,
    setVisibleLayouts,
    toggleLeftMenu,
    toggleRightMenu,
    leftContentVisible: visibleLayouts.includes(menuState.VISIBLE_LEFT),
    rightContentVisible: visibleLayouts.includes(menuState.VISIBLE_RIGHT),
    handleLayoutChange,
  }
}
